<template>
  <div class="filters">
    <accordion :open="true" v-if="showCategory">
      <template v-slot:header>
        Категории
      </template>
      <template v-slot:body>
        <div
          class="filters__item"
          v-for="category in CATEGORY"
          :key="category.id"
        >
          <input
            type="checkbox"
            :value="category.id"
            v-model="filter.category"
            :id="`cat${category.id}`"
          />
          <label :for="`cat${category.id}`">{{ category.title }}</label>
          <span class="filters__count">{{ category.count }}</span>
        </div>
      </template>
    </accordion>
    <accordion v-if="showDate">
      <template v-slot:header>
        Дата
      </template>
      <template v-slot:body>
        <v-date-picker
          v-model="dateRange"
          is-range
          @input="onInputDate"
          :is-expanded="true"
        />
      </template>
    </accordion>
    <div class="filters__group-btn">
      <button class="button-warning" @click="onFilter">
        применить
      </button>
      <button class="button-secondary mr-20" @click="filterCancel">
        сбросить
      </button>
    </div>
  </div>
</template>

<script>
import Accordion from "./Accordion";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "Filters",
  components: { Accordion },
  props: {
    showCategory: {
      type: Boolean,
      default: true
    },
    showDate: {
      type: Boolean,
      default: true
    },
    filterType: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      filter: {
        category: [],
        dateRange: null
      },
      dateRange: {
        start: new Date(),
        end: new Date()
      }
    };
  },
  computed: {
    ...mapGetters("category", ["CATEGORY"])
  },
  methods: {
    ...mapActions("category", [
      "GET_CATEGORY_POST",
      "GET_CATEGORY_EVENT",
      "GET_CATEGORY_RESIDENTS_EVENT",
      "GET_CATEGORY_STARTUP",
      "GET_CATEGORY_RESIDENT"
    ]),
    onFilter() {
      this.$eventBus.$emit("filter-list", this.filter);
      this.$emit("filter-list", this.filter);
    },
    filterCancel() {
      this.filter.category = [];
      this.filter.dateRange = null;
      this.dateRange = {
        start: new Date(),
        end: new Date()
      };
      this.$eventBus.$emit("filter-reset", this.filter);
      this.$emit("filter-reset", this.filter);
    },
    onInputDate(val) {
      this.filter.dateRange = {
        start: this.$moment(val.start)
          .startOf("day")
          .unix(),
        end: this.$moment(val.end)
          .endOf("day")
          .unix()
      };
    }
  },
  async mounted() {
    switch (this.filterType) {
      case "post":
        await this.GET_CATEGORY_POST();
        break;
      case "event":
        await this.GET_CATEGORY_EVENT();
        break;
      case "residents-event":
        if (this.residentId) {
          await this.GET_CATEGORY_RESIDENTS_EVENT(this.residentId);
        }
        break;
      case "startup":
        await this.GET_CATEGORY_STARTUP();
        break;
      case "resident":
        await this.GET_CATEGORY_RESIDENT();
        break;
    }
    this.$eventBus.$on("open-modal-filters", residentId => {
      if (this.filterType === "residents-event" && residentId) {
        this.GET_CATEGORY_RESIDENTS_EVENT(residentId);
      }
    });
  },
  beforeDestroy() {
    this.$eventBus.$off("open-modal-filters");
  }
};
</script>

<style scoped></style>
