<template>
  <div class="accordion">
    <div class="accordion__item">
      <div
        class="accordion__header"
        :class="{ 'accordion__header--open': toggle }"
        @click="headerClick"
      >
        <slot name="header"></slot>
        <svg class="accordion__header-icon">
          <use href="@/assets/img/sprite.svg#icon-arrow-right"></use>
        </svg>
      </div>
      <div class="accordion__body" v-show="toggle">
        <slot name="body"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Accordion",
  props: {
    open: {
      type: Boolean,
      default: null
    }
  },
  data() {
    return {
      toggle: false
    };
  },
  mounted() {
    if (this.open !== null) {
      this.toggle = this.open;
    }
  },
  methods: {
    headerClick() {
      this.toggle = !this.toggle;
      this.$emit("click");
    }
  }
};
</script>

<style scoped></style>
